.sub-calc {
    font-family: "Inter";
}

@media (min-width: 712px) and (min-height: 1138px) and (max-width: 1024px) and (max-height: 1368px) {
    .sub-calc {
        margin-bottom: 390px;
    }
}

.sub-calc__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;
}

@media (max-width: 467px) {
    .sub-calc__header {
        flex-direction: column;
        align-items: flex-start;
        row-gap: 10px;
    } 
}

.sub-calc__header h2 {
    font-weight: 600;
    font-size: 2.2rem;
    line-height: 136%;
    color: #4B465C;
    opacity: 0.9;
}

.sub-calc__header > div {
    border-radius: 6px;
    padding: 10px 1.8%;
    background-color: #FF9F43;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 20%;
    box-shadow: 0px 2px 4px rgba(165, 163, 174, 0.3);
    cursor: pointer;
}

@media (max-width: 1268px) {
    .sub-calc__header > div {
        width: 25%;
    }
}

@media (max-width: 795px) {
    .sub-calc__header > div {
        width: 30%;
    }
}

@media (max-width: 645px) {
    .sub-calc__header > div {
        width: 35%;
    }
}

@media (max-width: 542px) {
    .sub-calc__header > div {
        width: 40%;
    }
}

@media (max-width: 467px) {
    .sub-calc__header > div {
        width: 50%;
    }
}

@media (max-width: 367px) {
    .sub-calc__header > div {
        width: 60%;
    }
}

.sub-calc__header > div:hover {
    background-color: #C24624;
}

.sub-calc__header div div {
    width: 18px;
    aspect-ratio: 1;
    background: url("../assets/plus.png") center;
}

.sub-calc__header div p {
    font-weight: 500;
    font-size: 1.5rem;
    line-height: 187%;
    letter-spacing: 0.43px;
    color: white;
}

.sub-calc__main {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    row-gap: 26px;
    margin-bottom: 32px;
}

@media (max-width: 874px) {
    .sub-calc__main {
        justify-content: center;
        column-gap: 5%;
    }
}

.sub-calc__main__module {
    width: 31%;
    padding: 24px 2.1%;
    border-radius: 6px;
    border: 1px solid rgba(75, 70, 92, 0.4);
    position: relative;
    cursor: pointer;
}

@media (max-width: 874px) {
    .sub-calc__main__module {
        width: 36%;
    }
}

@media (max-width: 724px) {
    .sub-calc__main__module {
        width: 41%;
    }
}

@media (max-width: 624px) {
    .sub-calc__main__module {
        width: 61%;
    }
}

@media (max-width: 399px) {
    .sub-calc__main__module {
        width: 76%;
    }
}

.sub-calc__main__module.checked {
    border: 1px solid #FF9F43;
}

.sub-calc__main__module.disabled > h4,
.sub-calc__main__module.disabled > p {
    opacity: 0.4;
}

.sub-calc__main__module h4 {
    font-weight: 600;
    font-size: 1.8rem;
    line-height: 133%;
    color: #4B465C;
    opacity: 0.9;
    margin-bottom: 16px;
}

.sub-calc__main__module > p {
    font-weight: 400;
    font-size: 1.5rem;
    line-height: 147%;
    color: #4B465C;
    opacity: 0.8;
    margin-bottom: 24px;
}

.sub-calc__main__module__price {
    display: flex;
    justify-content: space-between;
}

.sub-calc__main__module__price p {
    font-weight: 400;
    font-size: 1.5rem;
    line-height: 147%;
    color: #FF9F43;
}

.sub-calc__main__module__checkbox {
    width: 18px;
    height: 18px;
    aspect-ratio: 1;
    border-radius: 4px;
    position: absolute;
    top: 27px;
    right: 6.9%; 
    z-index: 1;
}

.sub-calc__main__module__checkbox > label {
    display: block;
    width: 18px;
    height: 18px;   
    border: 1.5px solid rgba(75, 70, 92, 0.4);
    border-radius: 4px;
}

.sub-calc__main__module__checkbox input:checked + label {
    background: url("../assets/_Form\ Checkbox\ Button.png") center;
    border: none;
}

.sub-calc__consumtion {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;
}

@media (max-width: 849px) {
    .sub-calc__consumtion {
        flex-direction: column;
        align-items: center;
        row-gap: 10px;
    }
}

.sub-calc__consumtion > div {
    display: flex;
}

.sub-calc__consumtion div pre {
    color: #4B465C;
    font-size: 1.5rem;
    line-height: 147%;
    font-weight: 500;
    opacity: 0.5;
    font-family: "Inter";
}

@media (max-width: 399px) {
    .sub-calc__consumtion div pre {
        font-size: 1.1rem;
    }
}

.sub-calc__consumtion div pre:nth-child(1),
.sub-calc__consumtion div pre:nth-child(3) {
    opacity: 0.5;
}

.sub-calc__consumtion div pre:nth-child(2),
.sub-calc__consumtion div pre:nth-child(4) {
    opacity: 0.9;
}

.sub-calc button {
    width: 11.4%;
    height: 38px;
    background-color: #FF9F43;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-weight: 500;
    font-size: 1.5rem;
    line-height: 118%;
    letter-spacing: 0.43px;
    margin-left: auto;
    border-radius: 6px;
    margin-bottom: 70px;
    cursor: pointer;
}

@media (max-width: 784px) {
    .sub-calc button {
        width: 16.4%;
    }
}

@media (max-width: 544px) {
    .sub-calc button {
        width: 21.4%;
    }
}

@media (max-width: 424px) {
    .sub-calc button {
        width: 31.4%;
    }
}

.sub-calc button:hover {
    background-color: #C24624;
}

.sub-calc__substrate {
    width: 100vw;
    height: 100vh;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #4B465C;
    opacity: 0.3;
    display: none;
}

.sub-calc__substrate.active {
    display: block;
}